/* ********** Reset ********** */
html {
  box-sizing: border-box;
  font-family: var(--main-font), var(--alternate-font);
  font-size: var(--font-size);
  line-height: var(--line-height);
  color: var(--text-color);
  background-color: var(--bg-color); }

*,
*:after,
*:before {
  box-sizing: inherit;
  padding: 0;
  margin: 0; }

body {
  overflow-x: hidden; }

a {
  font-weight: bold;
  text-decoration: none;
  color: var(--link-color);
  transition: all .3s ease-out; }
  a:hover {
    color: var(--link-hover-color); }

img {
  margin: 0 auto;
  display: block;
  max-width: 100%;
  height: auto; }

li {
  margin-left: 1rem;
  list-style-position: inside; }

::selection {
  background-color: var(--main-color);
  color: var(--second-color); }

/* ********** Header & Panel Navigation ********** */
.Header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: var(--header-height);
  line-height: var(--header-height);
  background-color: var(--second-color); }
  .Header-container {
    position: relative;
    margin: 0 auto;
    max-width: var(--container-width); }

.Logo {
  position: absolute;
  z-index: 999; }
  .Logo a {
    font-size: 2rem;
    text-decoration: none;
    color: var(--main-color);
    transition: all .3s ease; }
    .Logo a:hover {
      opacity: .75; }
  .Logo img {
    padding: .25rem;
    width: auto;
    height: var(--header-height); }

.Panel {
  position: fixed;
  z-index: 998;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  overflow-y: auto;
  background-color: var(--second-modal-color);
  transition: transform .3s ease;
  /* De arriba */
  transform: translate(0, -100%);
  /* De la izquierda */
  transform: translate(-100%, 0);
  /* De abajo */
  transform: translate(0, 100%);
  /* De la derecha */
  transform: translate(100%, 0); }
  .Panel.is-active {
    transform: translate(0, 0); }
  .Panel-btn {
    position: absolute;
    z-index: 999;
    top: -.5rem;
    right: 0; }

.hamburger:focus {
  outline: 0;
  border: 0; }

.hamburger-inner, .hamburger-inner:after, .hamburger-inner:before {
  background-color: var(--main-color); }

.u-afterFixed {
  padding-top: var(--header-height); }

@media screen and (min-width: 64em) {
  .Header-container {
    display: flex; }
  .Logo {
    position: static;
    width: 10%; }
  .Panel {
    position: static;
    width: 90%;
    display: flex;
    overflow-y: visible;
    background-color: transparent;
    transform: translate(0, 0); }
    .Panel-btn {
      display: none; } }

/* ********** Menu ********** */
.Menu {
  margin: 0  auto;
  padding-top: var(--header-height);
  text-align: center; }
  .Menu ul {
    display: flex;
    flex-direction: column;
    list-style: none; }
  .Menu li {
    margin-left: 0; }
  .Menu a {
    display: block;
    font-size: 1.5rem;
    text-decoration: none;
    color: var(--main-color);
    transition: all .3s ease; }
    .Menu a a:hover {
      font-weight: bold;
      color: var(--bg-color);
      background-color: var(--main-color); }
  @media screen and (min-width: 64em) {
    .Menu {
      padding-top: 0;
      width: 100%; }
      .Menu ul:not(.sub-menu) {
        flex-direction: row;
        justify-content: space-evenly; }
      .Menu a {
        padding: 0 .5rem;
        font-size: 1.5rem;
        border-top: medium solid transparent; }
        .Menu a:hover {
          border-top: medium solid var(--bg-color);
          color: var(--bg-color);
          background-color: transparent; } }

/* WordPress Submenu Classes */
.sub-menu a {
  font-size: .85rem; }

@media screen and (min-width: 64em) {
  .sub-menu {
    position: absolute;
    top: var(--header-height);
    transition: all .3s ease-out;
    background-color: var(--second-modal-color);
    opacity: 0;
    visibility: hidden; }
    .sub-menu a {
      padding: 0 1rem;
      font-size: .85rem;
      text-align: left;
      border-top: medium solid transparent; }
  .menu-item-has-children {
    position: relative; }
    .menu-item-has-children:hover > .sub-menu {
      opacity: 1;
      visibility: visible; } }

/* ********** Footer ********** */
.Footer {
  background-color: var(--bg-alternate-color); }
  .Footer-container {
    margin: 0 auto;
    padding: 1rem;
    max-width: var(--container-width);
    min-height: var(--header-height);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center; }
    .Footer-container > div {
      width: 100%; }
    @media screen and (min-width: 64em) {
      .Footer-container {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        text-align: left; }
        .Footer-container > div {
          width: 50%; }
          .Footer-container > div:last-child {
            order: -1; } }

/* ********** Social Media ********** */
.SocialMedia ul {
  display: flex;
  justify-content: space-around;
  list-style: none; }

.SocialMedia .sr-text {
  display: none; }

.SocialMedia a {
  font-size: 1.5rem; }
  @media screen and (min-width: 64em) {
    .SocialMedia a {
      font-size: 2rem; } }
  .SocialMedia a::before {
    content: '\f19a';
    display: inline-block;
    font-family: 'Font Awesome 5 Brands'; }
  .SocialMedia a[href*="facebook"]::before {
    content: '\f09a'; }
  .SocialMedia a[href*="twitter"]::before {
    content: '\f099'; }
  .SocialMedia a[href*="github"]::before {
    content: '\f09b'; }
  .SocialMedia a[href*="codepen"]::before {
    content: '\f1cb'; }
  .SocialMedia a[href*="instagram"]::before {
    content: '\f16d'; }
  .SocialMedia a[href*="youtube"]::before {
    content: '\f167'; }

/* ********** WordPress Custom Header ********** */
.WP-Header {
  position: relative;
  width: 100%;
  height: 100vh; }
  .WP-Header-branding {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center; }
  .WP-Header-title {
    font-size: 5vw; }
  .WP-Header-description {
    width: 60%;
    font-size: 3.5vw; }
    @media screen and (min-width: 64em) {
      .WP-Header-description {
        width: 80%;
        font-size: 2.5vw; } }

.wp-custom-header {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: black; }
  .wp-custom-header > img,
  .wp-custom-header > iframe,
  .wp-custom-header > video {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 0%;
    opacity: .5; }

.wp-custom-header > button,
#wp-a11y-speak-polite {
  display: none; }

/* ********** Hero Image ********** */
.HeroImage {
  width: 100%;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-image: var(--bg-url);
  background-attachment: var(--bg-attach);
  background-size: var(--bg-size);
  background-position-x: var(--bg-x);
  background-position-y: var(--bg-y); }
  .HeroImage > div {
    min-height: inherit;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.5);
    color: #FFF; }
  .HeroImage h1 {
    padding: 0 1rem;
    font-size: 5vw; }
  .HeroImage p {
    width: 60%;
    font-size: 3.5vw; }
    @media screen and (min-width: 64em) {
      .HeroImage p {
        width: 80%;
        font-size: 2.5vw; } }
  .HeroImage img {
    border-radius: 50%; }

/* ********** Widget ********** */
.Widget {
  margin: 0 auto 2rem;
  max-width: calc(var(--container-width) / 2);
  padding: 1rem;
  background-color: var(--second-modal-color);
  color: var(--bg-color); }
  .Widget h3 {
    margin-bottom: 1rem; }
  .Widget mark {
    display: block;
    padding: 1rem;
    font-size: 1.5rem;
    text-align: right;
    background-color: var(--main-color);
    color: var(--bg-color); }

/* ********** Search ********** */
.Search {
  margin: 0 auto;
  display: flex;
  justify-content: center; }
  .Search label {
    padding: .5rem;
    outline: 0;
    border: 0;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--bg-color);
    background-color: var(--second-modal-color);
    transition: all .3s ease-out; }
    .Search label:hover {
      color: var(--main-color);
      opacity: .75; }
  .Search input[type="search"] {
    padding: .5rem;
    outline: 0;
    border: 0;
    font-family: var(--main-font);
    font-size: 1.5rem;
    color: var(--bg-color);
    background-color: var(--main-modal-color); }
    .Search input[type="search"]::placeholder {
      color: var(--bg-color); }
  .Search input[type="submit"],
  .Search .screen-reader-text {
    display: none; }

/* Styles CommentsList WordPress */
ol.commentlist {
  list-style: none;
  margin-bottom: 2rem; }

ol.commentlist li {
  margin-left: 0;
  margin-bottom: 2rem; }

ol.commentlist li.comment {
  border-bottom: thin dotted var(--main-color);
  padding: 1em; }

ol.commentlist li.comment div.vcard {
  font-size: 1rem; }

ol.commentlist li.comment div.vcard img.avatar {
  float: right;
  margin: 0 0 1rem 1rem;
  border-radius: 50%; }

ol.commentlist li.comment div.comment-metadata {
  font-size: .75rem; }

ol.commentlist li.comment p {
  margin-bottom: 1rem; }

ol.commentlist li.comment ul {
  list-style: none;
  margin: 0 0 1rem 2rem; }

ol.commentlist li.comment div.reply {
  font-size: .75rem; }

ol.commentlist li.comment ul.children {
  list-style: none;
  margin: 1rem 0 0; }

ol.commentlist li.comment ul.children li.depth-2 {
  margin: 0 0 .25rem .25rem;
  border-left: medium solid #333; }

ol.commentlist li.comment ul.children li.depth-3 {
  margin: 0 0 .25rem .25rem;
  border-left: medium solid #555; }

ol.commentlist li.comment ul.children li.depth-4 {
  margin: 0 0 .25rem .25rem;
  border-left: medium solid #777; }

ol.commentlist li.comment ul.children li.depth-5 {
  margin: 0 0 .25rem .25rem;
  border-left: medium solid #999; }

ol.commentlist li.even {
  background-color: #FEFEFE; }

ol.commentlist li.odd {
  background-color: #EFEFEF; }

ol.commentlist li.parent {
  border-left: thick solid #111; }

/* Styles CommentsForm WordPress */
form#commentform {
  padding: 1rem;
  background-color: var(--bg-color); }

form#commentform p {
  display: flex;
  flex-direction: column; }

form#commentform p.comment-notes {
  flex-direction: row; }

form#commentform label {
  font-weight: bold; }

form#commentform input:not([type="submit"]),
form#commentform textarea {
  padding: .5rem;
  border: thin solid var(--second-color);
  background-color: var(--bg-alternate-color);
  color: var(--main-color);
  font-size: 1rem;
  font-family: var(--main-font); }

span.required {
  color: var(--main-color);
  font-weight: bold; }

input#submit {
  margin-left: auto;
  padding: 1rem;
  width: 50%;
  font-family: var(--main-font);
  font-size: 1.25rem;
  font-weight: bold;
  background-color: var(--main-color);
  color: var(--bg-color);
  cursor: pointer;
  outline: 0;
  border: 0;
  transition: all .3s ease-out; }

input#submit:hover {
  background-color: var(--second-color); }

/* ********** Content ********** */
.Content-container {
  margin: 0 auto;
  max-width: var(--container-width);
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: repeat(3, auto);
  grid-template-areas: 'main' 'features' 'sidebar';
  grid-gap: 1rem; }

.Content-container.Page {
  grid-template-columns: 100%;
  grid-template-rows: repeat(2, auto);
  grid-template-areas: 'main' 'sidebar'; }

.Content-container.Page.FullWidth {
  grid-template-columns: 100%;
  grid-template-rows: auto;
  grid-template-areas: 'main'; }

.Content-container > * {
  padding: 1rem;
  background-color: var(--border-color); }

.Main {
  grid-area: main; }

.Main p {
  margin: 1rem 0; }

.Main hr {
  margin: 2rem auto;
  width: 50%;
  border: thin dashed var(--main-color); }

.Main hr:last-child {
  display: none; }

.Sidebar {
  grid-area: sidebar; }

.Pagination,
.Comments {
  grid-area: features; }

@media screen and (min-width: 64em) {
  .Content-container {
    margin: 1rem auto;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: repeat(2, auto);
    grid-template-areas: 'main sidebar' 'features sidebar'; }
  .Content-container.Page {
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto;
    grid-template-areas: 'main  sidebar'; }
  .Content-container.Page.Sidebar-left {
    grid-template-columns: 1fr 2fr;
    grid-template-rows: auto;
    grid-template-areas: 'sidebar main'; } }

/* ********** WP-Query ********** */
.WP-Query {
  margin: 1rem auto;
  padding: 1rem;
  max-width: var(--container-width);
  background-color: var(--border-color);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 1rem; }

.WP-Query figure {
  text-align: center; }
